body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

head {
  margin: 0 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.content {
  width: 60%;
  margin-left: 20%;
  height: 100vh;
}

h1 {
  margin: 0.5em;
}

.havainnot-table {
  border-collapse: collapse;
  background-color: white;
  margin-left: 1%;
  width: 98%;
}

th {
  text-align: left;
  background-color: rgb(29, 29, 156);
  color: #ffff;
  padding: 0.5em;
}

td {
  padding: 0.25em;
}

.havainnot-table tbody tr {
  border-bottom: 1px solid #dddddd;
}

.havainnot-table tbody tr:nth-of-type(even) {
  background-color: #f3f3f3;
}

.havainnot-table tbody tr:last-of-type {
  border-bottom: 2px solid rgb(29, 29, 156);
}

.loginScreen {
  width: 100%;
  height: 100vh;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login {
  display: grid;
  width: 470px;
  height: 500px;
  border-style: solid;
  border-width: thin;
  border-color: #dadce0;
  border-radius: 8px;
  background-color: white;
  row-gap: 15px;
}

.text {
  margin-left: 40px;
}

.inputBox {
  margin-top: 50px;
  display: grid;
}

.loginInput {
  font-size: 16px;
  height: 45px;
  margin-top: 0px;
  margin-bottom: 20px;
  margin-right: 40px;
  margin-left: 40px;
  border-style: solid;
  border-width: thin;
  border-color: #dadce0;
  border-radius: 4px;
}

.bt {
  display: flex;
  justify-content: center;
}

.loginButton {
  height: 40px;
  padding-right: 20px;
  padding-left: 20px;
  margin-bottom: 80px;
  margin-top: 20px;
  color: white;
  font-size: 16px;
  border-style: none;
  border-radius: 3px;
  background-color: #1a73e8;
  text-shadow: #1a73e8;
}

.header {
  display: flex;
  align-items: center;
}

.logoutButton {
  height: 20px;
  margin-left: 10px;
}
